/* ------- hr ---------- */
hr.hr-dark{
	background-color:rgba(0,0,0,1);
	margin: 10px;
	border: none;
}
hr.hr-light{
	background-color:rgba(0,0,0,0.2);
	margin: 10px;
	max-width: 95%;
}
hr.hr-muted{
	background-color:rgba(0,0,0,1);
	margin: 10px;
	max-width: 95%;	
}
hr.custom-hr{
	margin: 7px 0px;
	border: 0;
	border-top: 1px solid rgba(0,0,0,0.2);
}
hr.type-1{
	margin: 10px 0px;
    border: none;
    height: 1px;
    width: 100%;
    background-color: #ededed;
}
hr.type-2{
	margin: 10px 0px;
    border: none;
    height: 1px;
    width: 100%;
    background-color: #636363;
}
