// box shdow
.shadow-none{
	box-shadow: none;
	-webkit-box-shadow: none;
}
.shadow-none:hover{
	box-shadow: none;
	-webkit-box-shadow: none;
}
.box-shadow{
	box-shadow: 0px 4px 20px $default;
}
.box-shadow-dark{
	box-shadow: 0px 4px 10px $dark;
}
// shadow styling
.shadow-small{
	box-shadow: 0 3px 8px 0 rgba(0,0,0,0.08);
	-webkit-box-shadow: 0 3px 8px 0 rgba(0,0,0,0.08);
}
.shadow-small:hover {
	box-shadow: 0 3px 8px 0 rgba(0,0,0,0.15);
	-webkit-box-shadow: 0 3px 8px 0 rgba(0,0,0,0.15);
}
.shadow-normal{
	box-shadow: 0 4px 12px 0 rgba(0,0,0,0.08);
	-webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.08);
}
.shadow-normal:hover {
	box-shadow: 0 4px 12px 0 rgba(0,0,0,0.15);
	-webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.15);
}
.shadow-large{
	box-shadow: 0 8px 16px 0 rgba(0,0,0,0.1);
	-webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.1);
}
.shadow-large:hover {
	box-shadow: 0 8px 16px 0 rgba(0,0,0,0.15), 0 16px 32px 0 rgba(0,0,0,0.1);
	-webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.15), 0 16px 32px 0 rgba(0,0,0,0.1);
}
.shadow-exlarge{
	box-shadow: 0 12px 24px 0 rgba(0,0,0,0.1);
	-webkit-box-shadow: 0 12px 24px 0 rgba(0,0,0,0.1);
}
.shadow-exlarge:hover {
	box-shadow: 0 12px 24px 0 rgba(0,0,0,0.15), 0 24px 48px 0 rgba(0,0,0,0.1);
	-webkit-box-shadow: 0 12px 24px 0 rgba(0,0,0,0.15), 0 24px 48px 0 rgba(0,0,0,0.1);
}
.shadow-huge{
	box-shadow: 0 12px 24px 0 rgba(0,0,0,0.15), 0 24px 48px 0 rgba(0,0,0,0.1);
	-webkit-box-shadow: 0 12px 24px 0 rgba(0,0,0,0.15), 0 24px 48px 0 rgba(0,0,0,0.1);
}
.shadow-huge:hover {
	box-shadow: 0 16px 32px 0 rgba(0,0,0,0.2), 0 32px 64px 0 rgba(0,0,0,0.15);
	-webkit-box-shadow: 0 16px 32px 0 rgba(0,0,0,0.2), 0 32px 64px 0 rgba(0,0,0,0.15);
}
